// Modular Scale ==============================================================
@import 'mathsass/dist/_math';
@import 'modularscale-sass/stylesheets/modularscale';

@import 'build/sass/functions';
@import 'build/sass/variables';
@import 'build/sass/mixins';
// Only put styles that apply to the faqs page

.faqs-item {
    &.level-1 {
        padding-top: $gutters-200;
        margin-top: $gutters-200;
        border-top: 2px solid $primary;
        &:first-child {
            padding-top: 0;
            margin-top: 0;
            border-top: none;
        }
    }
    &.level-2 {
        padding-top: $gutters-100;
        margin-top: $gutters-100;
        border-top: 1px solid rgba($dark, 0.35);
    }
    &.level-3 {
        padding-top: $gutters-75;
        margin-top: $gutters-75;
        border-top: 1px solid rgba($dark, 0.25);
    }
    &.level-4 {
        padding-top: $gutters-50;
        margin-top: $gutters-50;
        border-top: 1px solid rgba($dark, 0.125);
    }
}

#mobile-faqs-nav-wrapper {
    .navbar > .container {
        padding: $gutters-25 $gutters-50;
    }
}
